$color-primary: #9356dc;
$color-secondary: #ff79da;
$color-tertiaire: #99e2d0;
$color-placeholder-header: #d3d3d3;
$color-back-body: #e5e7e9;
$color-police-text: #000;
$color-body-origine: #fff;
$color-header: #f0f0f2;
$color-shadow: #85929e;
$color-shadow-reservation: #cacfd2;

header {
  background-color: var($color-back-body);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  background-color: var($color-header);
  .imglogohaut {
    width: 100px;
    height: 30px;
    margin-top: 5px;
    margin-bottom: 5px;
  }
  .searchcity {
    background-color: var($color-back-body);
    box-shadow: inset 0em 0.5em 0.5em var($color-shadow);
    width: 100%;
    height: 40px;
    position: relative;
    text-align: center;
    display: flex;
    justify-content: center;
    font-weight: 100;
    font-style: normal;
    color: var($color-police-text);
    margin: 0;
    padding: 0.75rem;
    font-size: medium;
    .logoposition {
      margin-right: 10px;
      padding-top: 10px;
    }
  }
}
